* {
  padding: 0;
  margin: 0;
 
}

.container {
  min-width: 600px;
  width: 90%;
  margin: auto;
  padding-top: 100px;
  display: flex;
  padding: 0 10px;
  // padding-top: 100px;
  box-sizing: border-box;
  // width: 90%;
  // margin: auto;
  // padding-top: 100px;
  // display: grid;
  // grid-template-columns: 1fr 1fr;
}

.title {
  display: flex;
  border: 1px solid black;
  justify-content: center;
  align-items: center;
  // margin: 20px auto;
  font-size: 30px;
  margin: 10px auto 100px;
  // margin-top: 30px;
  max-width: 600px;
}

.left_content, .right_content {
  flex-basis: 50%;
}

.left_content-container {
  width: 50%;
}

.left_content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 0.5fr));
  grid-gap: 2rem;
}

.right_content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  text-align: center;
  color: black;
  position: fixed;
  top: 100px;
  right: 10px;
}

.right_content img {
  margin: 2rem;
  height: 200px;
}

.right_content h1 {
  text-transform: uppercase;
  font-size: bold;
  letter-spacing: 1px;
}

