.card {
  width: 250px;
  height: 300px;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  &:hover {
      // transform: scale(1.2);
      .photo-pok {
        transform: scale(0.8);
      }
  }

  .photo-conteiner {
  height: 200px;
  margin: 10px;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;

 .photo-pok {
  max-height: 100%;
  
 }
}

