.poke-type-conteiner {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  
}
.poke-type {
    border: 1px solid black;
    padding: 5px 20px;
    border-radius: 8px;
    font-weight: bold;
    background-color: #199eeb;
  }
 .poke-name {
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 5px;
 }
}



.photo-conteiner img {
    width: 200px;
  }

.abilities {
  width: 30%;
  margin: auto;
  display: flex;
  justify-content: space-around;
  gap: 5px;
  align-items: center;
  margin-top: 1rem;
}

.group {
  background-color: #199eeb;
  color: white;
  padding: 0.5rem;
  font-size: 12px;
  border-radius: 8px;
  border: 1px solid blue;
}

.base-stat {
  margin-top: 2rem;
}


.btn-group button {
  // display: inline;
  outline: none;
  border: none;
  max-width: 300px;
  // width: 150px;
  padding: 1rem 10rem;
  margin-bottom: 1rem;
  font-size: 30px;
  font-weight: bold;
  color: white;
  background-color: #199eeb;
  border-radius: 8px;

  
}
.loadBt {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
  }
.poke-info {
  margin-top: 30%;
  max-width: 300px;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  padding: 15px;
  
  &:hover {
    .poke-info-photo {
      transform: scale(1.2);
    }
  }
}